
<div>
  <virtual-scroll
    ref="virtualScroll"
    :data="list"
    :item-size="62"
    key-prop="id"
    @change="(virtualList) => tableData = virtualList">
    <el-table
      :data="tableData"
      height="500"
      row-key="id"
      tooltip-effect="dark"
      style="width: 100%">
      <!-- 多选 -->
      <el-table-column width="60">
        <template slot="header" slot-scope="{}">
          <el-checkbox v-model="isCheckedAll" :indeterminate="isCheckedImn" @change="onCheckAllRows"></el-checkbox>
        </template>
        <template slot-scope="{ row }">
          <el-checkbox v-model="row.checked" @change="onCheckRow"></el-checkbox>
        </template>
      </el-table-column>

      <el-table-column
        label="日期"
        width="120">
        <template slot-scope="scope">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址"
        show-overflow-tooltip>
      </el-table-column>
    </el-table>
  </virtual-scroll>
  <div style="margin-top: 20px">
    <el-button @click="setCurrent(list[1])">选中第二行</el-button>
    <el-button @click="setCurrent()">取消选择</el-button>
  </div>
</div>
